import React, { Component } from "react";
import Prism from "prismjs";
import "../../pages/prism.css";

class CodeSection extends Component {
  // componentDidMount = () => {
  //   Prism.highlightAll();
  // };
  render() {
    return (
      <pre>
        <code className="language-css">{this.props.children}</code>
      </pre>
    );
  }
}

export default CodeSection;
